// 导出report
import reportTLog from './reporter';
export {reportTLog};

//引入小程序补丁
import * as miniprogram  from '@ali/pixi-miniprogram-adapter';
reportTLog({
    message:'Engine_Adapter_Running'
});
// import polyfills. Done as an export to make sure polyfills are imported first
export * from './polyfill';
// export core
export * from './core';
reportTLog({
    message:'Engine_Core_Component_Running'
});
// export libs
import deprecation from './deprecation';
import * as accessibility from './accessibility';
import * as extract from './extract';
import * as extras from './extras';
import * as filters from './filters';
import * as interaction from './interaction';
import * as loaders from './loaders';
import * as mesh from './mesh';
import * as particles from './particles';
import * as prepare from './prepare';





// handle mixins now, after all code has been added, including deprecation
import { utils } from './core';
utils.mixins.performMixins();

//引入pixianimate小程序插件
import * as animate from './animate';


/**
 * Alias for {@link PIXI.loaders.shared}.
 * @name loader
 * @memberof PIXI
 * @type {PIXI.loader.Loader}
 */
const loader = loaders.shared || null;

export {
    accessibility,
    extract,
    extras,
    filters,
    interaction,
    loaders,
    mesh,
    particles,
    prepare,
    loader,
    miniprogram,
    animate
    
};

// Apply the deprecations
if (typeof deprecation === 'function')
{
    deprecation(exports);
}
/**
 * PIXI-animate中为Text原型注入了一些API,但PIXI本身打包后通过deprecation又把一些API弃用了
 * 所以需要在deprecation后执行animate的mixins注入
 */
// 解决 deprecation Text部分方法无法访问
require('./animate/mixins');

// Always export PixiJS globally.
miniprogram.windowAlias.PIXI = miniprogram.globalAlias.PIXI = exports; // eslint-disable-line

reportTLog({
    message:'Engine_Running'
});
