import {windowAlias} from '@ali/pixi-miniprogram-adapter';
export default (reportData, reportFrom = 'PIXI') => {
    try{
        // !!! 小部件中无tlog接口 不发送
        const isWidget = windowAlias.navigator.isWidget;
        if (my.call && !isWidget) {
            const time = getNowTime();
            Object.assign(reportData,{
                time,
                bizType:'AliMamaInteraction',
                reportFrom
            })
            console.log("TLOG REPORT", time, reportData);
            my.call("remoteLog", {
                type: 'error',
                key:reportData,
            });
        }
    }catch(e){
        console.error('TLogReport_Error',e);
    }
};
function getNowTime() {
    const time = new Date();
    let year = time.getFullYear();
    let month = time.getMonth() + 1;
    let day = time.getDate();
    let hour = time.getHours();
    let min = time.getMinutes();
    let sec = time.getSeconds();
    const ms = time.getMilliseconds();
    month = month < 10 ? `0${month}` : month;
    day = day < 10 ? `0${day}` : day;
    hour = hour < 10 ? `0${hour}` : hour;
    min = min < 10 ? `0${min}` : min;
    sec = sec < 10 ? `0${sec}` : sec;
    return `${year}-${month}-${day} ${hour}:${min}:${sec}:${ms}`;
}

